#navbarCollapse {
  display: none;
}

.topbarMenu ul {
  padding: 10px 0;
  margin: 0;

  .orange {
    font-weight: bold;
  }

  a {
    font-size: 14px;
    color: #262626;

    &:hover {
      color: $white-hover-color;
    }
  }

}

.topbar {
  border-bottom: 2px solid #e6e6e6;
  height: 58px;

  .orange {
    font-weight: 500;
  }

  .logo {
    padding: 7px 0 0;

    img {
      max-width: 170px;
      height: auto;
    }
  }

  .search-bl {
    padding-top: 13px;
  }

  .login {
    a {
      color: #262626;
    }

    padding: 0 35px 0 20px;
    line-height: 61px;
  }

  .send {
    padding-top: 6px;
  }

  .lang {
    white-space: nowrap;
    margin-left: 25px;
    padding-top: 21px;

    a {
      color: #262626;
      font-size: 14px;
      text-transform: uppercase;
      margin-left: 15px;
      line-height: 20px;

      &:hover {
        color: $white-hover-color;
      }

      &:not(:last-child) {
        padding-right: 15px;
        border-right: 2px solid #b6b6b6;
      }

      &.orange {
        font-weight: 700;
      }
    }
  }
  .but {
    margin-top: 0;
  }
  .row {
    align-items: center;
    height: 61px;
  }
  .user {
    padding: 0;
  }
}

.user {

  &-menu {
    margin-top: 7px;
    width: 191px;
    padding: 0;

    li {
      font-size: 14px;
      line-height: 23px;

      &:first-child {
        font-weight: 700;
        padding: 15px 10px;
        border-bottom: 2px solid #e0e0e0;
      }

      a {
        padding: 15px 10px;
        display: block;
        position: relative;

        &:hover:after {
          opacity: 0;
        }
      }

      &:not(:last-child):not(:first-child) a:after {
        content: '';
        position: absolute;
        display: block;
        left: 10px;
        right: 10px;
        bottom: -1px;
        border-bottom: 1px solid #e0e0e0;
        transition: opacity .3s;
      }



    }
  }

  &__logged {
    position: relative;
    margin: 5px 35px 0 20px;


  }

  &__avatar {
    position: relative;

    &:after {
      content: '';
      position: absolute;
      width: 8px;
      height: 8px;
      background: $orange;
      border-radius: 50%;
      overflow: hidden;
      border: 2px solid #fff;
      bottom: 2px;
      left: 0;
    }

    .image {
      overflow: hidden;
      width: 45px;
      height: 45px;
      border-radius: 50%;
    }
  }
}

#login-form {
  .login-block {
    display: block;
  }
}

.menu-item.active a {
  color: $orange
}