
.visible-xs,
.visible-sm,
.visible-md,
.visible-lg {
  display: none !important;
}

.visible-xs-block,
.visible-xs-inline,
.visible-xs-inline-block,
.visible-sm-block,
.visible-sm-inline,
.visible-sm-inline-block,
.visible-md-block,
.visible-md-inline,
.visible-md-inline-block,
.visible-lg-block,
.visible-lg-inline,
.visible-lg-inline-block {
  display: none !important;
}

@media (max-width: 767px) {
  .visible-xs {
    display: block !important;
  }
  table.visible-xs {
    display: table !important;
  }
  tr.visible-xs {
    display: table-row !important;
  }
  th.visible-xs,
  td.visible-xs {
    display: table-cell !important;
  }
}

@media (max-width: 767px) {
  .visible-xs-block {
    display: block !important;
  }
}

@media (max-width: 767px) {
  .visible-xs-inline {
    display: inline !important;
  }
}

@media (max-width: 767px) {
  .visible-xs-inline-block {
    display: inline-block !important;
  }
}

@media (min-width: 768px) and (max-width: 991px) {
  .visible-sm {
    display: block !important;
  }
  table.visible-sm {
    display: table !important;
  }
  tr.visible-sm {
    display: table-row !important;
  }
  th.visible-sm,
  td.visible-sm {
    display: table-cell !important;
  }
}

@media (min-width: 768px) and (max-width: 991px) {
  .visible-sm-block {
    display: block !important;
  }
}

@media (min-width: 768px) and (max-width: 991px) {
  .visible-sm-inline {
    display: inline !important;
  }
}

@media (min-width: 768px) and (max-width: 991px) {
  .visible-sm-inline-block {
    display: inline-block !important;
  }
}

@media (min-width: 992px) and (max-width: 1199px) {
  .visible-md {
    display: block !important;
  }
  table.visible-md {
    display: table !important;
  }
  tr.visible-md {
    display: table-row !important;
  }
  th.visible-md,
  td.visible-md {
    display: table-cell !important;
  }
}

@media (min-width: 992px) and (max-width: 1199px) {
  .visible-md-block {
    display: block !important;
  }
}

@media (min-width: 992px) and (max-width: 1199px) {
  .visible-md-inline {
    display: inline !important;
  }
}

@media (min-width: 992px) and (max-width: 1199px) {
  .visible-md-inline-block {
    display: inline-block !important;
  }
}

@media (min-width: 1200px) {
  .visible-lg {
    display: block !important;
  }
  table.visible-lg {
    display: table !important;
  }
  tr.visible-lg {
    display: table-row !important;
  }
  th.visible-lg,
  td.visible-lg {
    display: table-cell !important;
  }
}

@media (min-width: 1200px) {
  .visible-lg-block {
    display: block !important;
  }
}

@media (min-width: 1200px) {
  .visible-lg-inline {
    display: inline !important;
  }
}

@media (min-width: 1200px) {
  .visible-lg-inline-block {
    display: inline-block !important;
  }
}

@media (max-width: 767px) {
  .hidden-xs {
    display: none !important;
  }
}

@media (min-width: 768px) and (max-width: 991px) {
  .hidden-sm {
    display: none !important;
  }
}

@media (min-width: 992px) and (max-width: 1199px) {
  .hidden-md {
    display: none !important;
  }
}

@media (min-width: 1200px) {
  .hidden-lg {
    display: none !important;
  }
}

@media (max-width: 991px) {
  .article-block.image-top .article-block__image {
    height: 180px;
    background-position: center top;
  }
  .container {
    padding-left: 10px;
    padding-right: 10px;
  }
  .small-block-275px,
  .small-block-228px,
  .large-block,
  .h-550px {
    height: auto;
  }
  .block-bottom {
    position: relative;
    margin-top: 20px;
    bottom: auto;
    left: auto;
    right: auto;
  }

  body {
    padding-top: 55px;
  }

  .navbar {
    padding-top: 10px;
    padding-bottom: 10px;
    background: #ffffff;
    position: fixed;
    left: 0;
    top: 0;
    right: 0;
    z-index: 100;

    &-header {
      height: 35px;

      .logo-white {
        display: none;
      }
    }

    &.active {
      background: $grey;

      .navbar-header {
        .logo {
          display: none;
        }
        .logo-white {
          display: block;
        }
      }

      .navbar-toggle span {
        background-color: #fff;
      }
    }

    .nav {
      margin-top: 10px;

      & > li > a {
        color: $grey-light;
        font-size: 14px;
        line-height: 21px;
      }
    }

    &-brand img {
      height: 32px;
    }

    &-collapse {
      text-align: center;
    }

    .user-nav {
      color: #ffffff;
      margin-top: 20px;
      font-size: 18px;
      border-bottom: 2px solid #4d4d4d;;
      a {
        color: #ffffff;
      }
    }

    .lang {
      margin-top: 28px;
      color: #ffffff;
      font-size: 18px;

      a {
        color: #ffffff;
        text-transform: uppercase;
        padding: 0 20px;
      }
    }

    .search {
      margin-top: 35px;
      display: inline-block;
    }

    &-toggle {
      margin-top: 10px;
      position: relative;
      z-index: 2;

      span {
        display: block;
        width: 25px;
        height: 2px;
        background: $grey;
        margin-bottom: 5px;
      }
    }
  }
  .Label {
    position: static;
    margin-left: -5px;
    margin-right: -5px;
  }
  .page-section {
    padding: 0;
  }
  .section-1 {
    .closeIcon {
      top: 10px;
    }
  }
  .image-wide-block {
    height: 300px;
    margin-bottom: 10px;

    .block-bottom {
      position: absolute;
      bottom: 20px;
      left: 20px;
      right: 20px;
    }
  }

  .footer {
    text-align: center;

    .flexWrapper {
      display: block;
    }

    .mobile-menu {
      padding: 0;
      margin: 0;
      list-style: none;

      a {
        color: $grey-light;
        font-family: Ubuntu;
        font-size: 14px;
        line-height: 42px;

        &:hover {
          color: $white-hover-color;
        }
      }
    }

    .bottom-block {
      .social {
        order: 1;

        a:last-child {
          margin: 0;
        }
      }
      .logo {
        order: 2;
      }
    }
  }

  .out-container {
    @include out-container;
  }

  .out-row {
    @include out-row;
  }

  .large-block-col-12 .block,
  .block {
    padding: 10px;
  }
  .author-row {
    display: block;

    .moreExperts {
      position: relative;
      height: auto;
    }
  }
  .is-page-home {
    .hot-section .block {
      padding-top: 60px;
      padding-bottom: 60px;
    }
    .small-block-275px {
      margin-bottom: 10px;
    }
  }

  .author-row {
    .author-block {
      position: relative;
      z-index: 1;
    }
    .slick {

      &-slide {
        position: static;

        .moreExperts {
          position: absolute;
          left: 10px;
          right: 10px;
          top: 0;
          bottom: 0;
          background-color: transparent;
        }
      }
      &-active {
        .moreExperts {
          background-color: #333333;
        }
      }

      &-dots {
        padding: 0;
        margin: 0;
        list-style: none;
        text-align: center;

        li {
          display: inline-block;
          font-size: 0;
          overflow: hidden;
          padding: 0 3px;

          &.slick-active {
            button {
              background: $orange;
            }
          }

          button {
            height: 4px;
            border-radius: 100px;
            background-color: #dadada;
            border: 0;
            width: 30px;
          }
        }
      }
    }
  }

  .mfp-wrap.login-popup,
  .mfp-wrap.register-block {
    .mfp-content {
      width: 100%;
    }
  }
  #login-form {
    .login-block {
      display: flex;
      width: 100%;
      flex-wrap: wrap;
      padding-left: 10px;
      padding-right: 10px;

      .enter-block {
        text-align: center;
        display: block;

        .but {
          display: inline-block;
        }

        span {
          display: block;
          margin-top: 20px;
        }
      }

      .form-block {
        order: 1;
        padding-left: 0;
        padding-right: 0;

        .form-controls .uk-input, .form-controls input[type=email], .form-controls input[type=password], .form-controls input[type=text] {
          width: 100%;
        }

        &:before {
          display: none;
        }
      }
      .social-btn-group {
        order: 2;
        position: relative;
        margin-top: 30px;
        padding-bottom: 0;

        .google-btn {
          margin-bottom: 0;
        }

        &:before {
          content: '';
          border-top: 1px solid #e7e7e7;
          position: absolute;
          left: 0;
          width: 100%;
          top: 0;
          height: auto;
          background-color: transparent;
        }
      }
    }
  }

  .popup-title {
    display: none;
  }
  .mfp-bg {
    background: #f2f2f2;
    opacity: 1;
  }

  .section-1 .page-content {
    p {
      margin-top: 10px;
      color: #f2f2f2;
      font-size: 14px;
      line-height: 21px;
    }

    h1 {
      font-size: 16px;
      line-height: 24px;
    }
  }

  .alm-listing {
    display: block;

    .moreExperts {
      height: 350px;
    }
  }

  .expert-opinion-tags-block {
    .onoffswitch, span {
      vertical-align: middle;
    }
    .onoffswitch {
      display: inline-block;
      margin-left: 20px;
    }
    span {
      color: #a7a7a7;
      font-size: 18px;
    }
  }

  .single {
    p,
    h2,
    h3,
    h4,
    h5,
    h6 {
      font-size: 14px;
      margin-bottom: 20px;
      line-height: 21px;
    }
  }

  .article-content .article-bottom {
    .social a {

      i {
        font-size: 30px;
      }

      span {
        display: none;
      }
    }

    .author {
      .name {
        color: #a7a7a7;
        font-size: 12px;
        font-weight: normal;

        &:before {
          display: none;
        }
      }

      .img {
        display: inline-block;
      }
    }
  }

  .donate .block {
    &-header,
    &-description {
      font-size: 14px;
      line-height: 1.5;
    }
  }
  body.single .section-picture {
    height: 320px;

    & > .container {
      height: 100%;
    }

    .action-save {
      display: none;
    }

    .article-head {
      font-size: 16px;
      font-weight: 700;
      line-height: 24px;
    }
    .block {
      padding: 0;
      position: absolute;
      bottom: 20px;
      left: 10px;
      right: 10px;
      width: auto;

      &-bottom {
        display: none;
      }
    }
  }

  .expert-opinion-page {
    padding-bottom: 10px;
  }

  .user {
    &__logged {
      text-align: center;
      margin-top: 30px;
      margin-bottom: 30px;
    }
    &__avatar {

      display: inline-block;
    }
  }

  .profile-section .uk-form-column {
    padding-left: 10px;
    padding-right: 10px;
  }

  .account-page {

    &.topic-page-section {
      padding-bottom: 20px;
    }

    .profile-section {

      &.filled-profile-text-block {
        padding: 20px 10px;
      }

      .tags-list .add-tags, .tags-list__content .item {
        display: inline-block;
        margin-right: 0;
        clear: left;
      }
      .filled-profile-about {
        margin-top: 15px;
      }
      .filled-profile-top {
        margin-bottom: 20px;
      }

      p {
        font-size: 14px;
      }
      b {
        &, &.small-length {
          font-size: 14px;
          margin-right: 0;
          width: 50%;
          padding-right: 10px;
        }

      }
    }

    .articles-empty {
      padding: 20px 10px;

      h2 {
        font-size: 14px;
        font-weight: 700;
        line-height: 21px;
        margin: 0;
      }

      p {
        font-size: 14px;
        line-height: 21px;
        margin: 10px 0 0;

        a {
          text-decoration: underline;
        }
      }
    }

    &__head {
      margin-bottom: 10px;
      height: 50px;
      overflow: hidden;
      transition: height .3s;
      padding-top: 50px;
      position: relative;

      &.mobile-open {
        height: 150px;
      }

      .cat-block {
        display: block;
        font-weight: normal;
        text-transform: none;
        text-align: center;
        font-size: 14px;
        margin: 0;
        color: $grey;
        height: 50px;
        line-height: 50px;
        padding: 0;

        i {
          margin-left: 5px;
          font-size: 14px;
          display: none;
        }

        &.active {
          font-size: 18px;
          color: #ffffff;
          text-transform: uppercase;
          font-weight: 700;
          position: absolute;
          top: 0;
          left: 0;
          right: 0;

          i {
            display: inline-block;
          }
          &:before {
            display: none;
          }
        }
      }
    }

    &__mobile-edit {
      text-align: center;
      font-weight: 700;
      font-size: 14px;

      i {
        margin-left: 5px;
      }
    }
  }
  .donate-thanks {
    p {
      font-size: 14px;
    }
    h2 {
      font-size: 14px;
      margin-bottom: 10px;
    }
  }
  .donate-page {
    padding-top: 20px;

    .donate-stage-1-block h3,
    h1 {
      font-size: 16px;
    }
    p {
      font-size: 14px;
      line-height: 21px;
      text-align: center;
    }

    .donate-stage-1-block {
      padding: 20px;

      .donate-block {
        width: 48%;
        height: 120px;
        text-align: center;

        &.active {
          span {
            color: $grey;
          }
        }

        span {
          font-size: 18px;
        }
        i {
          display: block;
        }
      }

      .donate-section {
        flex-wrap: wrap;
      }
    }
  }

  .breadcrumb {
    display: none;
  }

  .registation-page {
    padding-top: 20px;
    padding-bottom: 0;

    h3,
    h1 {
      font-size: 14px;
      line-height: 22px;
      padding: 0;
    }

     .enter-block .but {
       margin-left: auto !important;
       margin-right: auto !important;
     }
    
    .mfp-wrap.register-block {

      .white-popup-block {
        padding-left: 10px;
        padding-right: 10px;
        display: flex;
        flex-wrap: wrap;
        width: 100%;
      }

      .mfp-content {
        padding-bottom: 0;

        .form-controls {
          .uk-input, input[type=email], input[type=password], input[type=text] {
            width: 100%;
          }
        }

        .social-btn-group,
        .form-block {
          padding: 0;

          &:before {
            display: none;
          }
        }

        .social-btn-group {
          order: 2;
          padding-right: 0 !important;
          margin-top: 20px;

          .disclaimer {
            width: 220px;
            margin: auto;
          }
        }

        .form-block {
          order: 1;
          padding-bottom: 35px;
          position: relative;

          &:after {
            content: '';
            position: absolute;
            left: 10px;
            right: 10px;
            bottom: 0;
            height: 1px;
            background: #e7e7e7;
          }
        }
      }
    }
  }

  .fb-login-popup {
    padding-left: 10px;
    padding-right: 10px;
    text-align: center;

    .but {
      display: inline-block;
      width: auto;
      padding: 13px 19px 12px;
      line-height: 1;
    }

    .facebook-done-block p {
      font-size: 14px;
      line-height: 21px;
    }
    .title {
      font-size: 14px;
      font-weight: 700;
      line-height: 1.2;
    }
  }

}
