// WordPress Generated Classes
// http://codex.wordpress.org/CSS#WordPress_Generated_Classes

// Media alignment
.alignnone {
  margin-left: 0;
  margin-right: 0;
  max-width: 100%;
  height: auto;
}
.aligncenter {
  display: block;
  margin: auto;
  height: auto;
}

img.aligncenter {
  display: inline-block;
  margin: 0 5px;
}

.alignleft,
.alignright {
  margin-bottom: 10px;
  height: auto;
}
