.footer {
  padding: 20px 0 40px;
  background: #333;
  color: #ffffff;

  .title {
    font-family: "Open Sans";
    font-size: 16px;
    font-weight: 700;
  }

  p {
    color: #ffffff;
    font-size: 14px;
    line-height: 24px;
  }

  .text {
    overflow: hidden;
  }

  .footer-title {
    font-weight: 700;
    color: $grey-light;
  }

  .footer-text {
    color: $grey-light;
  }

  .top {
    .flexWrapper {
      align-items: center;
    }
  }

  .social {
    margin-top: 15px;

    a {
      color: $grey-light;
      font-size: 25px;
      display: inline-block;
      margin-right: 20px;

      &:hover {
        color: $white-hover-color;
      }
    }
  }

  .footer-bottom {
    display: flex;
    justify-content: space-between;
    position: relative;
  }

  .follow {
    margin-top: 12px;

    .dropdown-content {
      margin-left: auto;

      .content-header p {
        color: #333333;
        margin-bottom: 17px;
      }
    }

    .wpcf7-form-control-wrap {
      vertical-align: middle;
    }

    .input {
      border-radius: 100px;
      border: 1px solid #e0e0e0;
      background-color: #ffffff;
      color: $grey-light;
      width: 253px;
      height: 33px;
      font-size: 14px;
      padding: 0 15px;
      font-style: italic;

      &:-moz-placeholder {
        color: $grey-light;
      }
      &:-ms-input-placeholder {
        color: $grey-light;
      }
      &::-webkit-input-placeholder {
        color: $grey-light;
      }

    }

  }

  .subscribe-btn.but {
    width: 117px;
    height: 31px;
    line-height: 31px;
    flex-shrink: 0;
  }

  .flexWrapper {
    justify-content: space-between;
    width: 100%;

    &.bottom-block {
      flex-wrap: nowrap;

      .flexItem:not(:first-child) {
        padding-left: 28px;

      }

      & > :last-child {
        width: 420px;
        flex-shrink: 0;
      }

      & > :first-child {
        flex-shrink: 0;
      }
    }
  }

  .flexItem > i {
    font-size: 26px;
    float: left;
    position: relative;
    top: -1px;
    margin-right: 13px;
  }

  hr {
    border: 0;
    height: 2px;
    background: #474747;
    margin: 15px 0 25px;
  }

  &-logo img {
    width: 170px;
  }
  .menu,
  .textwidget {
    margin-top: 20px;
  }

  .but {
    margin: 0;
  }

  .menu {
    list-style: none;
    padding-left: 0;
    margin: 20px 0 0;
    font-size: 14px;

    li {
      line-height: 23px;
    }

    a {
      color: #b6b6b6;

      &:hover {
        color: $white-hover-color;
      }
    }
  }

}