.expert-opinion {
  &-tags-block {

    .tags-block-title,
    .actual-tags {
      padding-top: 10px;
      display: inline-block;
    }

    li {
      margin-right: 10px;
    }
  }
  &-right-tags {
    white-space: nowrap;
    float: right;
    position: relative;

    .tags {
      height: 34px;
      border-radius: 100px;
      background-color: $grey;
      color: #fff;
      padding: 9px 13px;
      font-size: 14px;
      margin-left: 20px;
    }

    .sort {
      border-radius: 100px;
      border: 1px solid #e0e0e0;
      background-color: #ffffff;
      color: #a7a7a7;
      font-style: italic;
      font-size: 14px;
      width: 165px;
      height: 34px;
    }

    .btn-group {
      float: left;
      height: 34px;
      position: relative;
    }

    .dropdown {
      &-toggle {
        cursor: pointer;
        display: block;
      }

      &-menu {
        top: 41px;

        li {
          margin-right: 0;
        }

        &.dropdown-tags {
          width: 410px;
        }
      }
    }
  }
}

.archive-id-1 {
  .section-1 .page-content p {
    max-width: none;
  }
}

.expert-opinion-tags-block {
  padding-top: 10px;

  .cat-block {
    background-color: #fff;
    color: #333333;
    vertical-align: middle;
    transform: perspective(1px) translateZ(0);
    box-shadow: 0 0 1px transparent;
    position: relative;
    margin: 0 10px 15px 0;
    display: inline-block;
    padding: 6px 10px;
    font-size: 14px;
    font-weight: 700;
    text-transform: uppercase;

    &:not(.active) {
      border-radius: 2px;
      border: 1px solid #e0e0e0;
    }

    &.active {
      background-color: #eb5a23;
      color: #fff;

      &:before {
        pointer-events: none;
        position: absolute;
        z-index: -1;
        content: '';
        border-style: solid;
        -webkit-transition-duration: 0.3s;
        -o-transition-duration: 0.3s;
        transition-duration: 0.3s;
        -webkit-transition-property: transform;
        -webkit-transition-property: -webkit-transform;
        transition-property: -webkit-transform;
        -o-transition-property: transform;
        transition-property: transform;
        left: -webkit-calc(50% - 10px);
        left: calc(50% - 10px);
        bottom: 0;
        border-width: 10px 10px 0 10px;
        border-color: #EB5A23 transparent transparent transparent;
        -webkit-transform: translateY(10px);
        -ms-transform: translateY(10px);
        transform: translateY(10px);

      }
    }

  }
}